import videojs from 'video.js'
import { gsap, Expo } from 'gsap'
import forEach from 'lodash/forEach'
import '@less/theme/modules/overlay.less'

const Plugin = videojs.getPlugin('plugin')

// Default options for the plugin.
const defaults = {
  language: 'en',
  title: null,
  description: null,
  url: null,
  embedCode: null,
  showLogo: true,
  showShare: true
}

/**
 * An advanced Video.js plugin. For more information on the API
 *
 * See: https://blog.videojs.com/feature-spotlight-advanced-plugins/
 */
class NFBOverlay extends Plugin {
  /**
   * Create a NFBOverlay plugin instance.
   *
   * @param  {Player} player
   *         A Video.js Player instance.
   *
   * @param  {Object} [options]
   *         An optional options object.
   *
   *         While not a core part of the Video.js plugin architecture, a
   *         second argument of options is a convenient way to accept inputs
   *         from your plugin's caller.
   */
  constructor (player, options) {
    // the parent class will add player under this.player
    super(player)
    this.log.level(videojs.log.level())

    this.options = videojs.mergeOptions(defaults, options)

    this.playerEl = this.player.el()
    this.controlPrefix = `${this.player.id()}-`
    this.els = {}
    this.wasForcePausedBy = null
    this.isVisible = true

    this.showOverlay = this.showOverlay.bind(this)
    this.hideOverlay = this.hideOverlay.bind(this)
    this.revealBlock = this.revealBlock.bind(this)
    this.hideBlock = this.hideBlock.bind(this)

    this.player.ready(() => {
      this.init()
    })
  }

  init () {
    this.log('Initializing NFB Overlay plugin')
    this.player.addClass('vjs-nfb-overlay')

    if (this.options.title || this.options.description || this.options.showShare) {
      this.createTopBarContainer()
    }

    if (this.options.title) {
      this.createTitleContainer()
    }

    if (this.options.description || this.options.showShare) {
      this.createIconContainer()
    }

    if (this.options.showShare) {
      this.createShareIcon()
      this.createShareContainer()
    }

    if (this.options.description) {
      this.createInfoIcon()
      this.createInfoContainer()
    }

    if (this.options.showLogo) {
      this.createLogoContainer()
    }

    this.addListeners()
  }

  addListeners () {
    this.player.on('useractive', this.showOverlay)
    this.player.on('userinactive', this.hideOverlay)
    // this.player.on('play', this.hideOverlay)
    this.player.on('ended', this.showOverlay)
  }

  showOverlay () {
    if (!this.isVisible) {
      this.log('Show overlay')
      this.revealBlock(this.els.logoContainer, { slideLeft: true })
      this.revealBlock(this.els.topBarContainer, { slideDown: true })
      this.isVisible = true
    }
  }

  hideOverlay () {
    if (!this.player.paused()) {
      this.log('Hide overlay')
      this.hideBlock(this.els.logoContainer, { slideRight: true })
      this.hideBlock(this.els.topBarContainer, { slideUp: true })
      this.isVisible = false
    }
  }

  createTopBarContainer () {
    this.log('Creating top bar container')
    this.els.topBarContainer = this.playerEl.appendChild(document.createElement('div'))
    this.assignControlAttributes(this.els.topBarContainer, 'top-bar-container')
  }

  createTitleContainer () {
    this.log('Creating title container')
    this.els.titleContainer = this.els.topBarContainer.appendChild(document.createElement('div'))
    this.assignControlAttributes(this.els.titleContainer, 'title-container')

    let title = this.options.title
    if (this.options.url) {
      title = `<a href="${this.options.url}" title="${this.options.title}" target="_blank">${this.options.title}</a>`
    }
    this.els.titleContainer.innerHTML = title
  }

  createIconContainer () {
    this.log('Creating icon container')
    this.els.iconContainer = this.els.topBarContainer.appendChild(document.createElement('div'))
    this.assignControlAttributes(this.els.iconContainer, 'icon-container')
  }

  createInfoIcon () {
    this.log('Creating info icon')
    this.els.infoIcon = this.els.iconContainer.appendChild(document.createElement('button'))
    this.els.infoIcon.setAttribute('title', this.player.localize('More information'))
    this.els.infoIcon.innerHTML = '<i class="icon icon-info-o"></i>'
  }

  createInfoContainer () {
    this.log('Creating info container')
    this.els.infoContainer = this.playerEl.appendChild(document.createElement('div'))
    this.assignControlAttributes(this.els.infoContainer, 'info-container')
    this.hideBlock(this.els.infoContainer)
    this.els.infoContainer.innerHTML = `
      <button class="btn-close" title="${this.player.localize('Close')}"><i class="icon icon-delete-o"></i></button>
      <div class="content-container">
        <div class="auto-center">
          <h1>${this.options.title}</h1>
          <div class="copy"></div>
        </div>
      </div>
    `

    // Unescape HTML
    const description = document.createElement('textarea')
    description.innerHTML = this.options.description
    this.els.infoContainer.querySelector('.copy').innerHTML = description.value
    //

    this.els.closeInfoButton = this.els.infoContainer.querySelector('.btn-close')

    this.els.infoIcon.addEventListener('click', () => this.revealBlock(this.els.infoContainer, { forcePause: true, fade: true }))
    this.els.closeInfoButton.addEventListener('click', () => this.hideBlock(this.els.infoContainer, { fade: true }))
  }

  createShareIcon () {
    this.log('Creating share icon')
    this.els.shareIcon = this.els.iconContainer.appendChild(document.createElement('button'))
    this.els.shareIcon.setAttribute('title', this.player.localize('Share'))
    this.els.shareIcon.innerHTML = '<i class="icon icon-share-o"></i>'
  }

  createShareContainer () {
    this.log('Creating share container')
    this.els.shareContainer = this.playerEl.appendChild(document.createElement('div'))
    this.assignControlAttributes(this.els.shareContainer, 'share-container')
    this.hideBlock(this.els.shareContainer)
    this.els.shareContainer.innerHTML = `
      <button class="btn-close" title="${this.player.localize('Close')}"><i class="icon icon-delete-o"></i></button>
      <div class="content-container">
        <div class="auto-center">
          <div class="share-buttons">
            ${this.getFbShareButton()}
            ${this.getTwitterShareButton()}
            ${this.getEmailShareURL()}
            ${this.getLinkedinShareURL()}
          </div>
          ${this.options.url ? `<div class="input-container"><i class="icon icon-link"></i><input type="text" readonly value="${this.options.url}"/></div>` : ''}
          ${this.options.embed && this.options.embed.html ? `<div class="input-container"><i class="icon icon-embed"></i><input type="text" readonly value="${this.getEmbedCode()}"/></div>` : ''}
        </div>
      </div>
    `
    this.els.closeShareButton = this.els.shareContainer.querySelector('.btn-close')
    this.els.shareInput = this.els.shareContainer.querySelectorAll('input')

    this.els.shareIcon.addEventListener('click', () => this.revealBlock(this.els.shareContainer, { forcePause: true, fade: true }))
    this.els.closeShareButton.addEventListener('click', () => this.hideBlock(this.els.shareContainer, { fade: true }))
    this.els.shareInput.forEach(input => {
      input.addEventListener('click', () => input.select())
    })
  }

  getEmbedCode () {
    let html = this.options.embed.html
    if (this.options.embed.options) {
      forEach(this.options.embed.options, (val, name) => {
        html = html.replace(new RegExp(`\\(\\( *${name} *\\)\\)`, 'gi'), val)
      })
    }
    html = html.replace(/"/g, '&quot;')
    return html
  }

  getFbShareButton () {
    const url = `https://www.facebook.com/sharer/sharer.php?u=${encodeURI(this.options.url)}`
    return `<a href="${url}" target="_blank" title="Facebook"><i class="icon icon-facebook"></i></a>`
  }

  getTwitterShareButton () {
    const url = `https://twitter.com/intent/tweet?text=${encodeURI(this.options.url)}`
    return `<a href="${url}" target="_blank" title="Twitter"><i class="icon icon-twitter"></i></a>`
  }

  getEmailShareURL () {
    const url = `mailto:?subject=${this.options.title}&amp;body=${this.options.url}`
    return `<a href="${url}" target="_blank" title="${this.player.localize('Email')}"><i class="icon icon-enveloppe-0"></i></a>`
  }

  getLinkedinShareURL () {
    const url = `https://www.linkedin.com/sharing/share-offsite/?url=${encodeURI(this.options.url)}`
    return `<a href="${url}" target="_blank" title="Linkedin"><i class="icon icon-linkdin"></i></a>`
  }

  createLogoContainer () {
    this.log('Creating logo container')
    this.els.logoContainer = this.playerEl.appendChild(document.createElement('div'))
    this.assignControlAttributes(this.els.logoContainer, 'logo-container')
    this.els.logoContainer.innerHTML = `
      <a href="${this.options.language === 'fr' ? 'https://onf.ca' : 'https://nfb.ca'}" title="${this.player.localize('Go to NFB website')}" target="_blank">
        <i class="icon icon-nfb-onf-logo"></i>
      </a>
    `
  }

  revealBlock (container, options = {}) {
    if (container) {
      if (options.fade) {
        gsap.fromTo(
          container,
          {
            opacity: 0,
            y: -20,
            display: 'none'
          },
          {
            opacity: 1,
            y: 0,
            display: 'block',
            duration: 0.25,
            ease: Expo.easeOut
          })
      } else if (options.slideDown) {
        gsap.fromTo(
          container,
          {
            y: '-100%',
            opacity: 0.5
          },
          {
            duration: 1,
            y: '0%',
            opacity: 1,
            ease: Expo.easeOut
          })
      } else if (options.slideLeft) {
        gsap.fromTo(
          container,
          {
            x: '100%',
            opacity: 0.5
          },
          {
            duration: 0.5,
            x: '0%',
            opacity: 1,
            ease: Expo.easeOut
          })
      } else {
        container.classList.remove('_hide')
      }

      if (options.forcePause && !this.player.paused()) {
        this.wasForcePausedBy = container
        this.player.pause()
      }
    }
  }

  hideBlock (container, options = {}) {
    if (container) {
      if (options.fade) {
        gsap.to(container, {
          duration: 0.25,
          opacity: 0,
          y: -20,
          display: 'none',
          ease: Expo.easeOut
        })
      } else if (options.slideUp) {
        gsap.to(container, {
          duration: 1,
          opacity: 0.5,
          y: '-100%',
          ease: Expo.easeOut
        })
      } else if (options.slideRight) {
        gsap.to(container, {
          duration: 0.5,
          opacity: 0.5,
          x: '100%',
          ease: Expo.easeOut
        })
      } else {
        container.classList.add('_hide')
      }
      if (this.wasForcePausedBy === container) {
        this.wasForcePausedBy = null
        this.player.play()
      }
    }
  }

  assignControlAttributes (element, controlName) {
    element.id = this.controlPrefix + controlName
    element.className = `${this.controlPrefix + controlName} ${controlName}`
  }
}

// Define default values for the plugin's `state` object here.
NFBOverlay.defaultState = {}

// Include the version number.
NFBOverlay.VERSION = '1.0.0'

// Register the plugin with video.js.
videojs.registerPlugin('nfbOverlay', NFBOverlay)

export default NFBOverlay
